<template>
  <div id="app">
    <!-- <img alt="Vue logo" src="./assets/logo.png"> -->
    
    <div class="header">
      <ul class="header-button-left">
        <li>Cancel</li>
      </ul>

      <ul class="header-button-right" v-if="스텝 == 1" v-on:click="스텝 = 2">
        <li>Next</li> 
      </ul>

      <ul class="header-button-right" v-if="스텝 == 2" v-on:click="publish">
        <li>발행</li> 
      </ul>

      <img src="./assets/logo.png" class="logo">
    </div>

  <p>{{ $store.state.age }}</p>
  <button v-on:click="$store.commit('나이더하기')">버튼</button>
  <button v-on:click="$store.commit('이름바꾸기', 'Kim')">Kim으로 바꾸기버튼</button>

    <Body 
    v-on:글작성="작성한글 = $event"
    v-bind:내가누른필터="내가누른필터"
    v-bind:필터들="필터들"
    v-bind:업로드이미지="업로드이미지" 
    v-bind:스텝="스텝" 
    v-bind:포스팅들="포스팅들" />

    <!-- <div class="sample-box">임시 박스</div> -->
    <button v-on:click="more">더보기</button>

<!-- yarn add axios
npm install axios  -->


<!-- https://yogoho210.github.io/postdata2.json -->
    <!-- 서버 : XX가져다주세요~ 하면 XX갖다주는 기계
    통신 : GET/POST를 이용해서 서버랑 데이터를 주고받음
    AJAX : 서버랑 통신할 수 있게 도와주는 문법 (새로고침없이) -->

    <div class="footer">
      <ul class="footer-button-plus">
        <input v-on:change="upload" type="file" id="file" class="inputfile" >
        <label for="file" class="input-plus">+</label>
      </ul>
    </div>

    


  </div>
</template>

<script>
import axios from 'axios'
//import HelloWorld from './components/HelloWorld.vue'
import Body from './components/Body.vue'
import Postdata from './assets/postdata.js'
import EventBus from './EventBus.js'

export default {
  name: 'App',
  data(){
    return {
      스텝 : 0,
      업로드이미지 : '',
      작성한글 : '',
      포스팅들 : Postdata,
      필터들 : [ "normal", "clarendon", "gingham", "moon", "lark", "reyes", "juno", "slumber", "aden", "perpetua", "mayfair", "rise", "hudson", "valencia", "xpro2", "willow", "lofi", "inkwell", "nashville"],
      내가누른필터 : '',
    }
  },
  mounted(){
    EventBus.$on('선택한필터', (데이터) => {
      /* eslint-disable */
      console.log(데이터);
      this.내가누른필터 = 데이터;
    });
  },
  methods : {
    more(){
      axios.get('https://yogoho210.github.io/postdata2.json').then((a)=>{
        /* eslint-disable */
        console.log(a.data);
        this.포스팅들.push(a.data);
      })
    },
    publish(){
      //1. 스텝을 0으로 만들어주기
      //2. 내 게시물을 오브젝트로 예쁘게 만들어서
      var 내게시물 = {
        name: "Kimhyukmin",
        userImage: "https://placeimg.com/100/100/arch",
        postImage: this.업로드이미지,
        likes: 36,
        date: 'May 15',
        liked: false,
        caption: this.작성한글,
        filter: "perpetua"
      }
      //3. 내 게시물을 this.포스팅들 맨 앞에 추가
      this.포스팅들.unshift(내게시물);
      this.스텝 = 0;
    },

    upload(e){
      var 파일들 = e.target.files;
      var reader = new FileReader();
      reader.readAsDataURL(파일들[0]);
      reader.onload = (e) => {
        /* eslint-disable */
        console.log(e.target.result);
        this.업로드이미지 = e.target.result;
        this.스텝 = 1;
      }
      //1. 다음스텝으로 넘어감 
      //2. 업로드한 이미지를 DataURL로 압축함
      //3. 그 담에 이미지를 저기 파란곳에 표시해줌  
    }
  },
  components: {
    Body,
    //HelloWorld
  }
}
</script>

