import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

export let store = new Vuex.Store({
  state: {
    name : 'Park',
    age : 30,
    포스팅들 : [],
  },
  getters : { 
    이름가져오기(state){
        return state.name
    }
  },
  mutations: {
    나이더하기(state){
        state.age++
    },
    이름바꾸기(state, 값){
        state.name = 값
    }
  },
  actions : {
    // 데이터가져와(context, payload){
    //     axios.get('/more.json')
    //       .then( 가져온데이터 => {
    //          context.commit( "이름바꿔라" , 가져온데이터);
    //     });
    // }
    
  }  
})
