<template>
  <div v-on:click="filterClick" :class="['filter-item', 필터]" :style="`background-image:url(${업로드이미지})`" >
      <slot></slot>
  </div>
</template>

<script>
import EventBus from './../EventBus.js'

export default {
  methods : {
    filterClick(){
        //버스태워보냄
        EventBus.$emit('선택한필터', this.필터);
    }
  },
  props : {
      업로드이미지 : String,
      필터 : String
  }
}
</script>

<style>
.filter-item {
width: 100px;
height: 100px;
margin: 10px 10px 10px auto;
padding: 8px;
display: inline-block;
color : white;
background-size: cover;
background-position : center;
}

</style>